<template>
    <h2>글쓰기</h2>
    <div class="board-list">
        <table id="content">
            <tbody>
                <tr>
                    <th>제목</th>
                    <td><input type="text" v-model="btitle" name="btitle" id="btitle"></td>
                </tr>
                <tr>
                    <th>내용</th>
                    <td colspan="3"><textarea v-model="bcontent" name="bcontent" id="bcontent" cols="30" rows="10"></textarea></td>
                </tr>
                <tr>
                    <th>작성자</th>
                    <td><input type="text" v-model="bauthor" name="bauthor" id="bauthor"></td>
                    <th>비밀번호</th>
                    <td><input type="password" v-model="bpw" name="bpw" id="bpw"></td>
                </tr>
            </tbody>
        </table>
        <input type="button" value="작성" v-on:click="fnSave()">
    </div>
</template>
  
<script>

export default {
    data() { //변수생성
        return {
            btitle: '',
            bcontent: '',
            bauthor: '',
            bpw: ''
        }
    },
    methods: {
        fnSave() {
            this.form = {
                "btitle": this.btitle,
                "bcontent": this.bcontent,
                "bauthor": this.bauthor,
                "bpw": this.bpw
            }


            //INSERT
            this.$axios.post("http://localhost:8082/boardwrite", this.form)
                .then(() => {
                    alert('글이 저장되었습니다.')
                    location.href='/'
                }).catch((err) => {
                    if (err.message.indexOf('Network Error') > -1) {
                        alert('네트워크가 원활하지 않습니다.\n잠시 후 다시 시도해주세요.')
                    }
                })
        }
    }
}

</script>   